
import { defineComponent, computed, onMounted } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildGridAccessor } from "@/store/grid";
import { Grid } from "./graphics";
import { useGridWatchers } from "./grid-watchers";

type GridProps = {
	width: number;
	height: number;
	canvasId: string;
	padding: number;
	name: "compressorGrid" | "turbineGrid";
}

export default defineComponent({
	name: "Grid",
	props: {
		height: Number,
		width: Number,
		canvasId: String,
		padding: Number,
		name: String,
	},
	setup: (props) => {
		const { canvasId, padding, height, width, name } = props as GridProps;
		const store = useStore();

		const minAirflow = computed(buildGridAccessor(name, "minX", store));
		const maxAirflow = computed(buildGridAccessor(name, "maxX", store));
		const airflowInterval = computed(buildGridAccessor(name, "xStep", store));
		const minPressureRatio = computed(buildGridAccessor(name, "minY", store));
		const maxPressureRatio = computed(buildGridAccessor(name, "maxY", store));
		const pressureRatioStep = computed(buildGridAccessor(name, "yStep", store));

		onMounted(() => {
			const gridCanvas = document.getElementById(canvasId) as HTMLCanvasElement;

			const grid = new Grid({
				canvasRef: gridCanvas,
				height,
				width,
				padding,
				minX: minAirflow.value,
				maxX: maxAirflow.value,
				minY: minPressureRatio.value,
				maxY: maxPressureRatio.value,
				xInterval: airflowInterval.value,
				yInterval: pressureRatioStep.value
			});

			grid.drawGrid();
			grid.drawAxis();

			useGridWatchers({
				grid,
				gridActions: [grid.drawGrid.bind(grid), grid.drawAxis.bind(grid)],
				minX: minAirflow,
				maxX: maxAirflow,
				xStep: airflowInterval,
				minY: minPressureRatio,
				maxY: maxPressureRatio,
				yStep: pressureRatioStep
			})
		});
	},
})
