<template>
	<form>
		<fieldset>
			<div class="row">
				<label for="h-scale">Horizontal scale</label>
				<input
					id="h-scale"
					type="number"
					step="0.001"
					v-model="hScale">
			</div>
			<div class="row">
				<label for="h-offset">Horizontal offset</label>
				<input
					id="h-offset"
					type="number"
					v-model="hOffset">
			</div>
			<div class="row">
				<label for="v-scale">Vertical scale</label>
				<input
					id="v-scale"
					type="number"
					step="0.001"
					v-model="vScale">
			</div>
			<div class="row">
				<label for="v-offset">Vertical offset</label>
				<input
					id="v-offset"
					type="number"
					v-model="vOffset">
			</div>
		</fieldset>
	</form>
</template>

<script lang="ts">
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
import {
	buildAccessor,
	ExternalMap
} from "@/store/external-map";

type MapScaleControlsProps = {
	mapName: ExternalMap;
}

export default defineComponent({
	name: "MapScaleControls",
	props: {
		mapName: String
	},
	setup: (props) => {
		const { mapName } = props as MapScaleControlsProps;
		const store = useStore();

		const hOffset = computed(buildAccessor(mapName, "hOffset", store))
		const vOffset = computed(buildAccessor(mapName, "vOffset", store))
		const hScale = computed(buildAccessor(mapName, "hScale", store))
		const vScale = computed(buildAccessor(mapName, "vScale", store))

		return {
			hOffset,
			vOffset,
			hScale,
			vScale
		}
	}
})
</script>