<template>
	<Maps />
	<div class="calculation-container">
		<form>
			<div class="row">
				<label for="airflow">Mass airflow units</label>
				<fieldset id="airflow">
					<select
						id="airflow-units"
						v-model="airflowUnits">
						<option :value="MassAirflowUnits.kgs">kg/s</option>
						<option :value="MassAirflowUnits.lbsmin">Lbs/min</option>
					</select>
					<div class="row">
						<label for="min-airflow">Minimum airflow</label>
						<input
							id="min-airflow"
							type="number"
							v-model="minAirflow"
							:step="airflowUnitStep">
					</div>
					<div class="row">
						<label for="max-airflow">Maximum airflow</label><input
							id="max-airflow"
							type="number"
							v-model="maxAirflow"
							:step="airflowUnitStep">
					</div>
					<div class="row">
						<label for="airflow-interval">Airflow grid step</label><input
							id="airflow-interval"
							type="number"
							v-model="airflowInterval"
							step="any">
					</div>
				</fieldset>
			</div>
			<div class="row">
				<fieldset>
					<div class="row">
						<label for="min-pressure-ratio">Min pressure ratio</label>
						<input id="min-pressure-ratio" type="number" v-model="minPressureRatio" step="0.5">
					</div>
					<div class="row">
						<label for="max-pressure-ratio">Max pressure ratio</label>
						<input id="max-pressure-ratio" type="number" v-model="maxPressureRatio" step="0.5">
					</div>
					<div class="row">
						<label for="max-pressure-ratio">Pressure ratio step</label>
						<input id="max-pressure-ratio" type="number" v-model="pressureRatioStep" step="any">
					</div>
				</fieldset>
			</div>
		</form>
		<map-scale-controls :map-name="ExternalMap.Compressor" />
		<form>
			<fieldset>
				<div class="row">
					<label for="engine-displacement">Engine displacement (cc)</label>
					<input id="engine-displacement" type="number" v-model="engineDisplacement" step="1">
				</div>
				<div class="row">
					<label for="ambient-air-temperature">Ambient air temperature (C)</label>
					<input id="ambient-air-temperature" type="number" v-model="ambientAirTemperature">
				</div>
				<div class="row">
					<label for="ambient-air-pressure">Ambient air absolute pressure (mbar)</label>
					<input id="ambient-air-pressure" type="number" v-model="ambientAirPressure">
				</div>
			</fieldset>
		</form>
		<EngineMapPoints />
		<map-scale-controls :map-name="ExternalMap.Turbine" style="margin-top: 300px;" />
	</div>
</template>

<script lang="ts">
import {
	defineComponent,
	computed,
	watch,
	Ref,
	WritableComputedRef,
	ref,
} from "vue";
import { useStore } from "vuex";
import { ExternalMap } from "@/store/external-map";
import { buildAccessor as buildGridAccessor } from "@/store/grid";
import { buildAccessor as buildEngineMapAccessor } from "@/store/engine-map";
import Maps from "./Maps.vue"
import EngineMapPoints from "./EngineMapPoints.vue"
import MapScaleControls from "./MapScaleControls.vue"
import {
	MassAirflowUnits,
	MassAirflow,
	VolumeUnit,
	PressureUnit,
	TemperatureUnit
} from "@/physics/units";

type ValueModelizer = <T>(value: Ref, units: Ref<T>) => WritableComputedRef<number>
const modelizeValue: ValueModelizer = (value, units) => computed({
	get () { return value.value[units.value] },
	set (input: number) { value.value[units.value] = input }
})

export default defineComponent({
	name: "Home",
	components: {
		Maps,
		EngineMapPoints,
		MapScaleControls
	},
	setup: () => {
		const store = useStore();

		const airflowUnits = computed(buildEngineMapAccessor("airflowUnits", store));
		const minAirflow = computed(buildGridAccessor("compressorGrid", "minX", store))
		const maxAirflow = computed(buildGridAccessor("compressorGrid", "maxX", store))
		const airflowInterval = computed(buildGridAccessor("compressorGrid", "xStep", store))

		const minPressureRatio = computed(buildGridAccessor("compressorGrid", "minY", store));
		const maxPressureRatio = computed(buildGridAccessor("compressorGrid", "maxY", store));
		const pressureRatioStep = computed(buildGridAccessor("compressorGrid", "yStep", store));

		const engineDisplacement = modelizeValue(computed(buildEngineMapAccessor("displacement", store)), ref(VolumeUnit.cc));
		const ambientAirPressure = modelizeValue(computed(buildEngineMapAccessor("ambientAirPressure", store)), ref(PressureUnit.mbar));
		const ambientAirTemperature = modelizeValue(computed(buildEngineMapAccessor("ambientAirTemperature", store)), ref(TemperatureUnit.C));

		watch(
			() => airflowUnits.value,
			(newUnits, prevUnits) => {
				minAirflow.value = MassAirflow.convert(prevUnits, minAirflow.value, newUnits)
				maxAirflow.value = MassAirflow.convert(prevUnits, maxAirflow.value, newUnits)
				airflowInterval.value = MassAirflow.convert(prevUnits, airflowInterval.value, newUnits)
			}
		)

		const airflowUnitStep = computed(() => {
			if (airflowUnits.value === MassAirflowUnits.kgs) {
				return 0.05;
			}
			return 1;
		})

		return {
			MassAirflowUnits,
			ExternalMap,
			airflowUnits,
			minAirflow,
			maxAirflow,
			airflowInterval,
			minPressureRatio,
			maxPressureRatio,
			pressureRatioStep,
			airflowUnitStep,
			engineDisplacement,
			ambientAirPressure,
			ambientAirTemperature
		};
	},
});
</script>

<style lang="scss">
.calculation-container {
	display: flex;
	flex-direction: column;
}
</style>
