<template>
	<canvas
		:id="canvasId"
		:height="height"
		:width="width"
		@mousemove="handleHover"
		@mousedown="handleMouseDown"
		@mouseup="handleMouseUp"
		@contextmenu="handleRightClick" />
</template>

<script lang="ts">
import { defineComponent, computed, onMounted, watch, ref, onBeforeUnmount } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildGridAccessor } from "@/store/grid";
import { Grid } from "./graphics";
import {
	buildMapPointInserter,
	buildMapPointRemover,
	buildMapPointModifier,
	buildMapPointsGetter
} from "@/store/turbine-model";
import { useGridWatchers } from "./grid-watchers";
import { MassAirflowUnits, MassAirflow } from "@/physics/units";
import { useMouseControl } from "./mouse-control";

type TurbineMapProps = {
	width: number;
	height: number;
	canvasId: string;
	padding: number;
}

export default defineComponent({
	name: "TurbineMap",
	props: {
		height: Number,
		width: Number,
		canvasId: String,
		padding: Number,
	},
	setup: (props) => {
		const { canvasId, height, width, padding } = props as TurbineMapProps;
		const mapWidth = width - 2 * padding
		const mapHeight = height - 2 * padding

		const graphCanvas = ref({} as HTMLCanvasElement);
		const graphCanvasViewport = ref({ left: 0, top: 0 } as DOMRect);
		const windowResizeHandler = () => { graphCanvasViewport.value = graphCanvas.value?.getBoundingClientRect() }
		const originX = computed(() => graphCanvasViewport.value.left + padding)
		const originY = computed(() => graphCanvasViewport.value.top + padding)

		const store = useStore();

		const minAirflow = computed(buildGridAccessor("turbineGrid", "minX", store));
		const maxAirflow = computed(buildGridAccessor("turbineGrid", "maxX", store));
		const airflowInterval = computed(buildGridAccessor("turbineGrid", "xStep", store));
		const minPressureRatio = computed(buildGridAccessor("turbineGrid", "minY", store));
		const maxPressureRatio = computed(buildGridAccessor("turbineGrid", "maxY", store));
		const pressureRatioStep = computed(buildGridAccessor("turbineGrid", "yStep", store));
		const airflowUnits = ref(MassAirflowUnits.lbsmin);

		const setMapPoint = buildMapPointModifier(store);
		const insertMapPoint = buildMapPointInserter(store);
		const removeMapPoint = buildMapPointRemover(store);
		const turbineMapPointsRaw = buildMapPointsGetter(store);
		const turbineMapPoints = computed(
			() => turbineMapPointsRaw().map(p => ({
				y: p.airflow[airflowUnits.value],
				x: p.pressureRatio
			}))
		)

		onMounted(() => {
			graphCanvas.value = document.getElementById(canvasId) as HTMLCanvasElement;
			graphCanvasViewport.value = graphCanvas.value.getBoundingClientRect()
			window.addEventListener("resize", windowResizeHandler);
			window.addEventListener("scroll", windowResizeHandler);

			const graph = new Grid({
				canvasRef: graphCanvas.value,
				height,
				width,
				padding,
				minX: minAirflow.value,
				maxX: maxAirflow.value,
				minY: minPressureRatio.value,
				maxY: maxPressureRatio.value
			});

			graph.drawCurve(turbineMapPoints.value)

			useGridWatchers({
				grid: graph,
				gridActions: [() => { graph.drawCurve(turbineMapPoints.value) }],
				minY: minAirflow,
				maxY: maxAirflow,
				yStep: airflowInterval,
				minX: minPressureRatio,
				maxX: maxPressureRatio,
				xStep: pressureRatioStep
			})

			watch(
				() => turbineMapPoints.value,
				points => { graph.drawCurve(points) }
			)
		});

		onBeforeUnmount(() => {
			window.removeEventListener("resize", windowResizeHandler);
			window.removeEventListener("scroll", windowResizeHandler);
		})

		let contextPoint = -1;

		const findMapPointIndex = (pressureRatio: number, airflow: MassAirflow): number => {
			return turbineMapPointsRaw().findIndex((point) => {
				if (point.pressureRatio === pressureRatio || (point.pressureRatio * 1.05 > pressureRatio && point.pressureRatio * 0.95 < pressureRatio)) {
					if (
						point.airflow.SI === airflow.SI ||
						(point.airflow.SI * 1.05 > airflow.SI && point.airflow.SI * 0.95 < airflow.SI)
					) {
						return true;
					}
				}

				return false;
			})
		}

		const {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick
		} = useMouseControl<number, MassAirflow>({
			originX,
			originY,
			mapWidth,
			mapHeight,
			padding,
			minX: minAirflow,
			maxX: maxAirflow,
			minY: minPressureRatio,
			maxY: maxPressureRatio,
			transformYValue: a => new MassAirflow(a, airflowUnits.value),
			transformXValue: a => a,
			onHover: (pressureRatio, airflow) => {
				if (contextPoint > -1) {
					setMapPoint(
						{
							pressureRatio,
							airflow,
						},
						contextPoint
					)
				}
			},
			onMouseDown: (pressureRatio, airflow) => {
				contextPoint = findMapPointIndex(pressureRatio, airflow);
			},
			onMouseUp: () => { contextPoint = -1 },
			onRightClick: (pressureRatio, airflow) => {
				const pointIndex = findMapPointIndex(pressureRatio, airflow);

				if (pointIndex < 0) {
					insertMapPoint({
						pressureRatio,
						airflow,
					});
				} else {
					removeMapPoint(turbineMapPointsRaw()[pointIndex]);
				}
			},
		})

		return {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick,
			airflowUnits
		}
	},
})
</script>

<style lang="scss" scoped>
.tooltip {
	display: none;
	background: #C8C8C7;
	margin-left: 30px;
	padding: 10px;
	position: absolute;
	z-index: 1000;
	width: 200px;
	height: 100px;
}
</style>
