
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
import {
	buildAccessor,
	ExternalMap
} from "@/store/external-map";

type MapScaleControlsProps = {
	mapName: ExternalMap;
}

export default defineComponent({
	name: "MapScaleControls",
	props: {
		mapName: String
	},
	setup: (props) => {
		const { mapName } = props as MapScaleControlsProps;
		const store = useStore();

		const hOffset = computed(buildAccessor(mapName, "hOffset", store))
		const vOffset = computed(buildAccessor(mapName, "vOffset", store))
		const hScale = computed(buildAccessor(mapName, "hScale", store))
		const vScale = computed(buildAccessor(mapName, "vScale", store))

		return {
			hOffset,
			vOffset,
			hScale,
			vScale
		}
	}
})
