import { AIR_SPECIFIC_GAS_CONSTANT } from "@/physics/constants"
import {
	MassAirflow,
	Pressure,
	Temperature,
	Volume,
} from "@/physics/units"

type KgM3 = number

type IGetAirDensity = (p: { temperature: Temperature; pressure: Pressure }) => KgM3
export const getAirDensity: IGetAirDensity = ({ temperature, pressure }) => (
	pressure.SI / (temperature.SI * AIR_SPECIFIC_GAS_CONSTANT)
);

type IGetVolume = (p: {
	displacement: Volume;
	volumetricEfficiency?: number;
	throttling?: number;
}) => Volume
export const getVolume: IGetVolume = ({
	displacement,
	volumetricEfficiency = 1,
	throttling = 1,
}) => {
	displacement["cubic meters"] = displacement.SI * volumetricEfficiency * throttling
	return displacement;
};

type IGetVolumeFlow = <T extends number>(p: { rpm: number; volume: T }) => number
export const getVolumeFlow: IGetVolumeFlow = ({ rpm, volume }) => (
	(volume * rpm) / (2 * 60)
);

type IGetRPMfromMAF = (p: {
	massAirflow: MassAirflow;
	displacement: Volume;
	temperature: Temperature;
	pressure: Pressure;
}) => number
export const getRPMfromMAF: IGetRPMfromMAF = ({
	massAirflow, displacement, temperature, pressure
}) => {
	const airDensity = getAirDensity({ temperature, pressure })
	const volumeFlow = massAirflow.SI / airDensity;
	const rpm = volumeFlow * 60 * 2 / displacement.SI;
	return rpm
}

type IGetMAF = (p: {
	rpm: number;
	displacement: Volume;
	temperature: Temperature;
	pressure: Pressure;
}) => MassAirflow
export const getMAF: IGetMAF = ({
	rpm,
	displacement,
	temperature,
	pressure,
}) => {
	const volume = getVolume({ displacement });
	return new MassAirflow(getVolumeFlow({ rpm, volume: volume.SI }) * getAirDensity({ temperature, pressure }))
};
