/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { PA_PER_PSI } from "@/physics/constants";
import { Unit } from "./Unit";

export enum PressureUnit {
	Pa = "pascal",
	kPa = "kilopascal",
	bar = "bar",
	mbar = "milibar",
	psi = "psi"
}

export class Pressure implements Unit {
	private _pa = 0;
	private _kpa = 0;
	private _bar = 0;
	private _mbar = 0;
	private _psi = 0;

	constructor(value: number, unit = PressureUnit.Pa) {
		this[unit] = value;
	}

	get SI () { return this._pa }

	set [PressureUnit.Pa] (value: number) {
		this._pa = value;
		this._kpa = value / 1000;
		this._bar = value / 100000;
		this._mbar = value / 100;
		this._psi = value / PA_PER_PSI;
	}
	get [PressureUnit.Pa] () { return this._pa }

	set [PressureUnit.kPa] (value: number) {
		this._pa = value * 1000;
		this._kpa = value;
		this._bar = value / 100;
		this._mbar = value * 10;
		this._psi = value * 1000 / PA_PER_PSI;
	}
	get [PressureUnit.kPa] () { return this._kpa }

	set [PressureUnit.bar] (value: number) {
		this._pa = value * 100000;
		this._kpa = value * 100;
		this._bar = value;
		this._mbar = value * 1000;
		this._psi = value * 100000 / PA_PER_PSI;
	}
	get [PressureUnit.bar] () { return this._bar }

	set [PressureUnit.mbar] (value: number) {
		this._pa = value * 100;
		this._kpa = value / 10;
		this._bar = value / 1000;
		this._mbar = value;
		this._psi = value * 100 / PA_PER_PSI;
	}
	get [PressureUnit.mbar] () { return this._mbar }

	set [PressureUnit.psi] (value: number) {
		this._psi = value;
		this._pa = PA_PER_PSI * value;
		this._kpa = this._pa / 1000;
		this._bar = this._pa / 100000;
		this._mbar = this._pa / 100;
	}
	get [PressureUnit.psi] () { return this._psi }

	getRelativePressure(ambient: Pressure): Pressure {
		return new Pressure(this.SI - ambient.SI)
	}

	getPressureRatio(ambient: Pressure): number {
		return this.SI / ambient.SI
	}

	multiply(ratio: number): Pressure {
		return new Pressure(this.SI * ratio)
	}

	compareTo(another: Pressure): number {
		return this.SI - another.SI;
	}
}