import { ABSOLUTE_ZERO_CELSIUS } from "@/physics/constants";
import { Unit } from "./Unit";

export enum TemperatureUnit {
	K = "kelvin",
	C = "celsius",
	F = "farenheit",
}

const celsiusToFarenheit = (celsius: number): number => celsius * 9 / 5 + 32;
const farenheitToCelsius = (farenheit: number): number => (farenheit - 32) * 5 / 9;
const kelvinToFarenheit = (kelvin: number): number => celsiusToFarenheit(kelvin - ABSOLUTE_ZERO_CELSIUS);
const farenheitToKelvin = (farenheit: number): number => farenheitToCelsius(farenheit) + ABSOLUTE_ZERO_CELSIUS;

export class Temperature implements Unit {
	private _K = 0;
	private _C = -1 * ABSOLUTE_ZERO_CELSIUS;
	private _F = kelvinToFarenheit(0);

	constructor(value: number, unit = TemperatureUnit.K) {
		this[unit] = value;
	}

	get SI () { return this._K }

	set [TemperatureUnit.K] (value: number) {
		this._K = value;
		this._C = value - ABSOLUTE_ZERO_CELSIUS;
		this._F = kelvinToFarenheit(value);
	}
	get [TemperatureUnit.K] () { return this._K; }

	set [TemperatureUnit.C] (value: number) {
		this._K = value - ABSOLUTE_ZERO_CELSIUS;
		this._C = value;
		this._F = celsiusToFarenheit(value);
	}
	get [TemperatureUnit.C] () { return this._C; }

	set [TemperatureUnit.F] (value: number) {
		this._K = farenheitToKelvin(value);
		this._C = farenheitToCelsius(value);
		this._F = value;
	}
	get [TemperatureUnit.F] () { return this._F; }
}
