import { Module, Store } from "vuex";

export const NAMESPACE = "grid"

export interface GridConfig {
	minX: number;
	maxX: number;
	xStep: number;
	minY: number;
	maxY: number;
	yStep: number;
}

type GridType = "compressorGrid" | "turbineGrid"

export type GridConfigState = Record<GridType, GridConfig>;

export const buildAccessor = <T extends keyof GridConfig>(
	grid: GridType,
	property: T,
	store: Store<{}>
): {
	get: () => GridConfig[T];
	set: (input: GridConfig[T]) => void;
} => ({
		get: () => store.getters[`${NAMESPACE}/grid`][grid][property],
		set: (input) => store.commit(`${NAMESPACE}/${property}`, { grid, [property]: input })
	})

export const grid: Module<GridConfigState, {}> = {
	namespaced: true,
	state: {
		compressorGrid: {
			minX: 0,
			maxX: 60,
			xStep: 5,
			minY: 1,
			maxY: 4,
			yStep: 0.25
		},
		turbineGrid: {
			minX: 1,
			maxX: 3.75,
			xStep: 0.25,
			minY: 0,
			maxY: 25,
			yStep: 2.5
		}
	},
	getters: {
		grid: state => state
	},
	mutations: {
		minX: (state, { grid, minX }: { grid: GridType; minX: number }) => state[grid].minX = minX,
		maxX: (state, { grid, maxX }: { grid: GridType; maxX: number }) => state[grid].maxX = maxX,
		xStep: (state, { grid, xStep }: { grid: GridType; xStep: number }) => state[grid].xStep = xStep,
		minY: (state, { grid, minY }: { grid: GridType; minY: number }) => state[grid].minY = minY,
		maxY: (state, { grid, maxY }: { grid: GridType; maxY: number }) => state[grid].maxY = maxY,
		yStep: (state, { grid, yStep }: { grid: GridType; yStep: number }) => state[grid].yStep = yStep,
	}
}
