import { Module, Store } from "vuex";
import {
	MassAirflowUnits,
	Pressure,
	PressureUnit,
	Temperature,
	TemperatureUnit,
	Volume,
	VolumeUnit
} from "@/physics/units";

export const NAMESPACE = "engineMap";

type MapPoint = { pressure: Pressure; rpm: number }

export type EngineMapState = {
	airflowUnits: MassAirflowUnits;
	mapPressureUnits: PressureUnit;
	points: MapPoint[];
	displacement: Volume;
	ambientAirTemperature: Temperature;
	ambientAirPressure: Pressure;
}

const namespaced = (input: keyof EngineMapState | "addPoint" | "removePoint" | "point"): string => `${NAMESPACE}/${input}`;

export const buildMapPointsGetter = (store: Store<{}>) => (): MapPoint[] => store.getters[namespaced("points")]
export const buildMapPointInserter = (store: Store<{}>) => (point: MapPoint) => store.dispatch(namespaced("addPoint"), point)
export const buildMapPointRemover = (store: Store<{}>) => (point: MapPoint) => store.dispatch(namespaced("removePoint"), point)
export const buildMapPointModifier = (store: Store<{}>) => (point: MapPoint, index: number) => store.commit(namespaced("point"), { point, index })

export const buildAccessor = <T extends keyof EngineMapState>(
	property: T,
	store: Store<{}>
): {
	get: () => EngineMapState[T];
	set: (input: EngineMapState[T]) => void;
} => ({
		get: () => store.getters[namespaced(property)],
		set: (input) => store.commit(namespaced(property), input)
	})

export const engineMap: Module<EngineMapState, {}> = {
	namespaced: true,
	state: {
		airflowUnits: MassAirflowUnits.lbsmin,
		mapPressureUnits: PressureUnit.bar,
		points: [
			{ pressure: new Pressure(2.5, PressureUnit.bar), rpm: 3000 },
			{ pressure: new Pressure(2.5, PressureUnit.bar), rpm: 5300 },
			{ pressure: new Pressure(2.5, PressureUnit.bar), rpm: 6500 }
		],
		displacement: new Volume(1984, VolumeUnit.cc),
		ambientAirPressure: new Pressure(101325, PressureUnit.Pa),
		ambientAirTemperature: new Temperature(15, TemperatureUnit.C)
	},
	getters: {
		airflowUnits: ({ airflowUnits }) => airflowUnits,
		mapPressureUnits: ({ mapPressureUnits }) => mapPressureUnits,
		points: ({ points }) => points,
		displacement: ({ displacement }) => displacement,
		ambientAirPressure: ({ ambientAirPressure }) => ambientAirPressure,
		ambientAirTemperature: ({ ambientAirTemperature }) => ambientAirTemperature,
	},
	mutations: {
		airflowUnits: (state, airflowUnits) => state.airflowUnits = airflowUnits,
		mapPressureUnits: (state, mapPressureUnits) => state.mapPressureUnits = mapPressureUnits,
		point: (state, { point, index }) => {
			state.points[index].pressure[state.mapPressureUnits] = point.pressure[state.mapPressureUnits]
			state.points[index].rpm = point.rpm
		},
		points: (state, points) => state.points = points,
		displacement: (state, displacement) => state.displacement = displacement,
		ambientAirPressure: (state, ambientAirPressure) => state.ambientAirPressure = ambientAirPressure,
		ambientAirTemperature: (state, ambientAirTemperature) => state.ambientAirTemperature = ambientAirTemperature,
	},
	actions: {
		addPoint: ({ commit, state }, point: MapPoint) => {
			const nextPoints = [
				...state.points.filter(p => p.rpm !== point.rpm),
				point
			].sort((a, b) => a.rpm - b.rpm)

			commit("points", nextPoints)
		},
		removePoint: ({ commit, state }, point: MapPoint) => {
			const nextPoints = state.points.filter(p => p.rpm !== point.rpm)

			commit("points", nextPoints)
		}
	}
}