import { Ref, watch } from "vue"
import { Grid } from "./graphics"

type GridParamGetters = [
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
	() => Ref<number>["value"],
]

type GridWatchersConfig = {
	grid: Grid;
	gridActions: (() => void)[];
	minX: Ref<number>;
	maxX: Ref<number>;
	xStep: Ref<number>;
	minY: Ref<number>;
	maxY: Ref<number>;
	yStep: Ref<number>;
}

export const useGridWatchers = (config: GridWatchersConfig) => {
	const {
		grid,
		gridActions,
		minX,
		maxX,
		xStep,
		minY,
		maxY,
		yStep
	} = config;

	watch<GridParamGetters>(
		[
			() => minX.value,
			() => maxX.value,
			() => xStep.value,
			() => minY.value,
			() => maxY.value,
			() => yStep.value,
		],
		(newValues) => {
			const [
				minX,
				maxX,
				xInterval,
				minY,
				maxY,
				yInterval,
			] = newValues

			grid.setup({
				minX,
				maxX,
				xInterval,
				minY,
				maxY,
				yInterval
			})

			gridActions.forEach(a => a())
		}
	)
}