import { Ref } from "vue"

type MouseEventHandler<T, K> = (xValue: T, yValue: K) => void

type MouseControlConfig<XUnit, YUnit> = {
	originX: Ref<number>;
	originY: Ref<number>;
	mapWidth: number;
	mapHeight: number;
	padding: number;
	minX: Ref<number>;
	maxX: Ref<number>;
	minY: Ref<number>;
	maxY: Ref<number>;
	tooltip?: Ref<HTMLElement>;
	transformXValue: (input: number) => XUnit;
	transformYValue: (input: number) => YUnit;
	onHover: MouseEventHandler<XUnit, YUnit>;
	onMouseDown: MouseEventHandler<XUnit, YUnit>;
	onMouseUp: MouseEventHandler<XUnit, YUnit>;
	onRightClick: MouseEventHandler<XUnit, YUnit>;
}

export const useMouseControl = <X, Y>({
	originX,
	originY,
	mapHeight,
	mapWidth,
	padding,
	minX,
	maxX,
	minY,
	maxY,
	tooltip,
	transformXValue,
	transformYValue,
	onHover,
	onMouseDown,
	onMouseUp,
	onRightClick,
}: MouseControlConfig<X, Y>) => {
	const getMapDataFromAtCanvasXY = ({ x, y }: { x: number; y: number }): [number, number] | [] => {
		const mapY = y - originY.value
		const mapX = x - originX.value

		if (mapY < 0 || mapY >= mapHeight) { return [] }
		if (mapX < 0 || mapX >= mapWidth) { return [] }

		 return [
			((mapX / mapWidth) * (maxX.value - minX.value)) + minX.value,
			((1 - (mapY / mapHeight)) * (maxY.value - minY.value)) + minY.value,
		]
	}

	const handlePointerDataDisplay = (e: MouseEvent) => {
		if (!tooltip) { return }
		if (tooltip.value.style.display === "none") {
			tooltip.value.style.display = "block";
		}

		tooltip.value.style.left = e.pageX - originX.value + padding  + "px"
		tooltip.value.style.top = e.pageY - originY.value + padding + "px"
	}

	const handleHover = (e: MouseEvent) => {
		const [ xValue, yValue ] = getMapDataFromAtCanvasXY(e);

		if (!xValue || !yValue) { return }

		if (tooltip) {
			tooltip.value.style.display = "none"
		}

		handlePointerDataDisplay(e)

		onHover(transformXValue(xValue), transformYValue(yValue));
	}

	const handleMouseDown = (e: MouseEvent) => {
		const [ xValue, yValue ] = getMapDataFromAtCanvasXY(e);

		if (!xValue || !yValue) { return }

		onMouseDown(transformXValue(xValue), transformYValue(yValue));
	}

	const handleMouseUp = (e: MouseEvent) => {
		const [ xValue, yValue ] = getMapDataFromAtCanvasXY(e);

		if (!xValue || !yValue) { return }

		onMouseUp(transformXValue(xValue), transformYValue(yValue));
	}

	const handleRightClick = (e: MouseEvent) => {
		e.preventDefault();

		const [ xValue, yValue ] = getMapDataFromAtCanvasXY(e);

		if (!xValue || !yValue) { return }

		onRightClick(transformXValue(xValue), transformYValue(yValue));
	}

	return {
		handleHover,
		handleMouseDown,
		handleMouseUp,
		handleRightClick
	}
}