<template>
	<div class="map-points-container">
		<div
			v-for="(point, index) in points"
			:key="index">
			<form v-on:submit.prevent="removeEngineMapPoint(point)">
				<fieldset class="map-point">
					<input type=number v-model="point.pressure" step="0.01" />
					<input type=number v-model="point.rpm" step="100" />
					<button type="submit">remove</button>
				</fieldset>
			</form>
		</div>
		<form v-on:submit.prevent="addEngineMapPoint">
			<fieldset>
				<label for="engine-pressure">Pressure (bar)</label>
				<input id="engine-pressure" type=number v-model="enginePressure" step="0.01" />
				<label for="engine-speed">Engine speed (rpm)</label>
				<input id="engine-speed" type=number v-model="engineSpeed" step="100" />
			</fieldset>
			<button type="submit">Add!</button>
		</form>
	</div>
</template>

<script lang="ts">
import { defineComponent, computed, ref } from "vue"
import { useStore } from "vuex"
import {
	buildMapPointsGetter,
	buildMapPointInserter,
	buildMapPointRemover,
	buildAccessor as buildMapPointAccessor,
} from "@/store/engine-map"
import { Pressure } from "@/physics/units";

export default defineComponent({
	name: "EngineMapPoints",
	setup: () => {
		const store = useStore();

		const enginePressure = ref(1);
		const engineSpeed = ref(1000);

		const pressureUnits = computed(buildMapPointAccessor("mapPressureUnits", store));
		const rawPoints = computed(buildMapPointsGetter(store));
		const points = computed(() => rawPoints.value.map(point => {
			return {
				get pressure () {
					return point.pressure[pressureUnits.value]
				},
				set pressure (p: number) {
					point.pressure[pressureUnits.value] = p
				},
				get rpm () {
					return point.rpm
				},
				set rpm (r: number) {
					point.rpm = r
				}
			}
		}))

		const insertMapPoint = buildMapPointInserter(store);

		const removeEngineMapPoint = buildMapPointRemover(store);

		const addEngineMapPoint = () => {
			insertMapPoint({ pressure: new Pressure(enginePressure.value, pressureUnits.value), rpm: engineSpeed.value })

			return false;
		}

		return {
			points,
			enginePressure,
			engineSpeed,
			addEngineMapPoint,
			removeEngineMapPoint
		}
	}
})
</script>

<style lang="scss" scoped>
.map-points-container {
	display: flex;
	flex-direction: column;
}

.map-point {
	display: flex;
}
</style>