import { LBS_PER_KG } from "@/physics/constants";
import { Unit } from "./Unit";

export enum MassAirflowUnits {
	gs = "g/s",
	kgs = "kg/s",
	lbsmin = "lbs/min"
}

const convertToLbsMin = (input: number) => {
	return input * LBS_PER_KG * 60;
}

const convertToKgS = (input: number) => {
	return (input / 60) / LBS_PER_KG;
}

export class MassAirflow implements Unit {
	private _gs = 0;
	private _kgs = 0;
	private _lbsmin = 0;

	static convert(inputType: MassAirflowUnits, value: number, outputType: MassAirflowUnits): number {
		if (inputType === outputType) { return value; }

		return new MassAirflow(value, inputType)[outputType];
	}

	constructor(value: number, units = MassAirflowUnits.kgs) {
		this[units] = value;
	}

	get SI () {
		return this._kgs;
	}

	set [MassAirflowUnits.gs] (value: number) {
		this._gs = value;
		this._kgs = value / 1000;
		this._lbsmin = convertToLbsMin(this._kgs);
	}
	get [MassAirflowUnits.gs] () { return this.round(this._gs) }

	set [MassAirflowUnits.kgs] (value: number) {
		this._gs = value * 1000;
		this._kgs = value;
		this._lbsmin = convertToLbsMin(this._kgs);
	}
	get [MassAirflowUnits.kgs] () { return this.round(this._kgs) }

	set [MassAirflowUnits.lbsmin] (value: number) {
		this._lbsmin = value;
		this._kgs = convertToKgS(value);
		this._gs = this._kgs * 1000;
	}
	get [MassAirflowUnits.lbsmin] () { return this.round(this._lbsmin) }

	round(input: number) {
		return Math.round(input * 10000) / 10000
	}
}