import { Module, Store } from "vuex";

export const NAMESPACE = "externalMap"

export enum ExternalMap {
	Compressor = "compressorMap",
	Turbine = "turbineMap"
}
interface ExternalMapConfig {
	hScale: number;
	hOffset: number;
	vScale: number;
	vOffset: number;
}
export type ExternalMapConfigState = Record<ExternalMap, ExternalMapConfig>

const namespaced = (input: keyof ExternalMapConfig): string => `${NAMESPACE}/${input}`;

export const buildAccessor = <T extends keyof ExternalMapConfig>(
	map: ExternalMap,
	property: T,
	store: Store<{}>
): {
	get: () => ExternalMapConfig[T];
	set: (input: ExternalMapConfig[T]) => void;
} => ({
		get: () => store.getters[`${NAMESPACE}/maps`][map][property],
		set: (input) => store.commit(namespaced(property), { map, [property]: input })
	})

export const externalMap: Module<ExternalMapConfigState, {}> = {
	namespaced: true,
	state: {
		[ExternalMap.Compressor]: {
			hScale: 0.687,
			hOffset: -52,
			vScale: 0.381,
			vOffset: 91,
		},
		[ExternalMap.Turbine]: {
			hScale: 0.5055,
			hOffset: -9,
			vScale: 0.6385,
			vOffset: 63,
		}
	},
	getters: {
		maps: state => state
	},
	mutations: {
		hScale: (state, { map, hScale }: { map: ExternalMap; hScale: number }) => { state[map].hScale = hScale },
		hOffset: (state, { map, hOffset }: { map: ExternalMap; hOffset: number }) => { state[map].hOffset = hOffset },
		vScale: (state, { map, vScale }: { map: ExternalMap; vScale: number }) => { state[map].vScale = vScale },
		vOffset: (state, { map, vOffset }: { map: ExternalMap; vOffset: number }) => { state[map].vOffset = vOffset },
	}
}
